import React, { FC } from 'react';
import { useSelector } from 'react-redux';
import {
  CellEditProps,
  ClickEnum,
  cnpjFormatter,
  ColumnsProps,
  decimalFormatter,
  DefaultSortProps,
  enumColumn,
  FilterType,
  getExtraData,
  OrderType,
  SearchDataGrid,
  SearchViewProps,
  WindowComponent,
} from 'summer';
import { hasEmpresa } from '../../../../dependencies/empresaDependency';
import { hasEmpresaMCG } from '../../../../dependencies/empresaMCGDependency';
import { CLASS_NAME, DoacaoPartidoPolitico } from '../../../../models/dtos/mcg/doacaoPartidoPolitico';
import { FORMA_DOACAO_ENUM_VALUES } from '../../../../models/enumerated/mcg/formaDoacaoEnum';
import { TIPO_BENEFICIARIO_ENUM_VALUES } from '../../../../models/enumerated/mcg/tipoBeneficiarioEnum';
import { Reducers } from '../../../../store/ducks';
import { GlobalState } from '../../../../store/ducks/global.duck';

const defaultSorted: DefaultSortProps[] = [
  {
    dataField: 'cnpj',
    order: OrderType.ASC,
  },
];

const cellEditProps: CellEditProps = {
  blurToSave: true,
  mode: ClickEnum.CLICK,
};

const DoacaoPartidoPoliticoSearchView: FC<SearchViewProps<DoacaoPartidoPolitico>> = props => {
  const globalReducer = useSelector<Reducers, GlobalState>(state => state.globalReducer);

  const columns: ColumnsProps[] = [
    {
      dataField: 'cnpj',
      editable: false,
      filter: {
        type: FilterType.TEXT,
      },
      formatter: cnpjFormatter,
      sort: true,
      text: 'CNPJ',
    },
    enumColumn(
      {
        dataField: 'tipo',
        editor: true,
        filter: true,
        sort: true,
        text: 'Tipo Beneficiário',
      },
      TIPO_BENEFICIARIO_ENUM_VALUES
    ),
    enumColumn(
      {
        dataField: 'forma',
        editor: true,
        filter: true,
        sort: true,
        text: 'Forma de Doação',
      },
      FORMA_DOACAO_ENUM_VALUES
    ),
    {
      dataField: 'valor',
      editable: true,
      filter: {
        type: FilterType.TEXT,
      },
      formatter: decimalFormatter,
      sort: true,
      text: 'Valor',
    },
  ];
  return (
    <WindowComponent
      title="Manutenção de Doações a Campanhas Eleitorais"
      useGlobalParameter={true}
      globalParameter={globalReducer.globalParameter}
      dependencies={[() => hasEmpresa(globalReducer.globalParameter), () => hasEmpresaMCG(globalReducer.globalParameter)]}
    >
      <SearchDataGrid
        {...props}
        {...props.searchDataGridProps}
        dtoClassName={CLASS_NAME}
        columns={columns}
        cellEditProps={cellEditProps}
        defaultSorted={defaultSorted}
        extraData={getExtraData(props, 'DoacaoPartidoPoliticoEditView', CLASS_NAME)}
      />
    </WindowComponent>
  );
};

export default DoacaoPartidoPoliticoSearchView;
